.latin-tenses-table {
  border-collapse: collapse;
  box-shadow: 0 5px 10px #e1e5ee;
  background-color: white;
  text-align: left;
  overflow: hidden;
}

.latin-tenses-table thead {
  box-shadow: 0 5px 10px #e1e5ee;
}

.latin-tenses-table th {
  padding: 1rem 2rem;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  font-size: 0.7rem;
  font-weight: 900;
}

.latin-tenses-table td {
  padding: 1rem 2rem;
}

.latin-tenses-table a {
  text-decoration: none;
  color: #2962ff;
}

.latin-tenses-table tr:nth-child(even) {
  background-color: #f4f6fb;
}
